import React, {Component } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components'
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import ImgListItem from './ImgListItem'
import Platform from 'react-platform-js'
import SETTINGS from '../config/gucci'
import TopScollImages from './TopScollImages'

class TitleGridArea extends Component {
    divRef = React.createRef();
    constructor(props) {
        super(props);
        let windowWidth = (window.innerWidth > 0) ? (window.innerWidth) : (window.outerWidth);
        this.updateSize = this.updateSize.bind(this);
        this.leftClick = this.leftClick.bind(this);
        this.rightClick = this.rightClick.bind(this);
        this.handleSetIndex = this.handleSetIndex.bind(this);
        this.imgClicked = this.imgClicked.bind(this);
        this.zoomInFunc = this.zoomInFunc.bind(this);
        this.state = {
            gridData: this.props.gridData,
            srcPrefix: this.props.srcPrefix,
            areaTitle: this.props.areaTitle,
            collectionWidth: windowWidth * (windowWidth > 885 ? 0.6 : 0.9),
            index:0,
            isAnimation: false,
            clickToZoomOut: this.props.clickToZoomOut,
            zoomOutted: false,
            extraBottom: this.props.extraBottom,
            currScollIndex: 0
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateSize);
      }
    
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateSize);
    }

    updateSize() {
        let windowWidth = (window.innerWidth > 0) ? (window.innerWidth) : (window.outerWidth);
        if (windowWidth > 880) {
            this.setState({collectionWidth: windowWidth * 0.6});
        } else {
            this.setState({collectionWidth: windowWidth * 0.9});
        }
        this.render();
    }

    leftClick() {
        if (this.state.isAnimation === true) {
            return;
        }
        const rows = 2
        const numberOfPages = rows * 3;
        const page = parseInt(this.state.gridData.length / numberOfPages + (this.state.gridData.length % numberOfPages === 0 ? 0 : 1));
        let after = this.state.index + 1;
        if (after < page || (after > page && after - page < 0.00001)) {
            this.handleSetIndex(after, true);
        }
    }

    rightClick() {
        if (this.state.isAnimation === true) {
            return;
        }
        this.setState({isAnimation: true});
        let after = this.state.index - 1;
        if (after >= 0 || (after < 0 && -after < 0.00001)) {
            this.handleSetIndex(after, false);
        } else {
            this.setState({isAnimation: false});
        }
    }

    handleSetIndex(after, plus) {
        if (this.timer) {
            clearTimeout(this.timer);
        }
        this.timer = setTimeout(()=>{
            let step = 0.5;
            this.setState({
                index: plus ? (this.state.index + step) : (this.state.index - step)
            });
            this.render();
            if (Math.abs(this.state.index - after) > 0.00001) {
                this.handleSetIndex(after, plus);
            } else {
                clearTimeout(this.timer);
                this.setState({isAnimation: false});
            }
        },50); 
    }

    imgClicked(idx) {
        if (!this.state.clickToZoomOut) {
            return;
        }
        this.setState({zoomOutted: true,
            currScollIndex:idx});
        this.render();
    }

    zoomInFunc() {
        // 要求点击空白才消失，点击轮播跳跳到对应image
        var x = window.event.x;
        var y = window.event.y;
        const parentDom = ReactDOM.findDOMNode(this).parentNode;
        let target = parentDom.childNodes[5].childNodes[3].childNodes[0].childNodes[0].childNodes[0];
        let rect = target.getBoundingClientRect();
        if (x < rect.x || y < rect.y || x > (rect.x + rect.width) || y > (rect.y + rect.height)) {
            this.setState({zoomOutted: false});
            this.render();
        }
    }
    
    render() {        
        const rows = 2;
        const numberOfPages = rows * 3;
        // 每页展示6个
        const page = parseInt(this.state.gridData.length / numberOfPages + (this.state.gridData.length % numberOfPages === 0 ? 0 : 1));
        const cols = (this.state.gridData.length % numberOfPages === 0) ? page * 3 : (parseInt(this.state.gridData.length / numberOfPages) * 3 + ((this.state.gridData.length % numberOfPages) >= 3 ? 3 : (this.state.gridData.length % numberOfPages)));
        const totalWidth = page * this.state.collectionWidth;
        const factor = Platform.DeviceType === "mobile" ? 1 : 1;
        const containerFactor = (rows === 2) ? 1.834348356 : (1.834348356 * 2);
        const containerHeight = this.state.collectionWidth / containerFactor;
        const OutterDiv = styled.div`
            margin-top:50pt;
            margin-left:20%;
            margin-right:20%;
            margin-bottom:-60px;
            overflow-x: hidden !important;
            @media screen and (min-width: 885px) and (max-width: 1024px) {
                margin-top:35pt;
                margin-bottom:-50px;
            }
            @media screen and (min-width: 0px) and (max-width: 885px) {
                margin-bottom:-50px;
                margin-top:35pt;
                margin-left:6%;
                margin-right:6%;
            }
        `;
        const MiddleDiv = styled.div`
            margin-top: 50pt;
            overflow-x: hidden !important;
            -webkit-overflow-scrolling:touch;
            .MuiGridListTile-root {
                margin-bottom: 20pt;
                margin-right: 0%; 
                width: ${parseFloat(this.state.collectionWidth / 3.0 / factor)}px !important;
            }
            .MuiGridList-root {
                overflow-x: hidden !important;
                width: ${parseFloat(totalWidth / factor)}px !important;
                margin-left:${-this.state.index * this.state.collectionWidth / factor}px !important;
            }
            a {
                font-weight:bold !important;
            }

            @media screen and (min-width: 0px) and (max-width: 885px) {
                margin-top: 20pt;
            }
        `;
        const TitleBarDiv = styled.p`
            text-align:center;
            color:white;
            font-size:17pt;
            border-color:rgba(255,255,255,0.7);
            border-width:1.5pt;
            border-style: solid;
            width:66%;
            margin-left:17%;
            display:block;
            position: relative;
            height:30pt;
            padding-top:12px;
            padding-bottom:6px;
            @media screen and (min-width: 885px) and (max-width: 1024px) {
                font-size:8pt;
                height:12pt;
                border-width:1pt;
                padding-top:5px;
                width:40%;
                margin-left:35%;
            }
            @media screen and (min-width: 0px) and (max-width: 885px) {
                font-size:6pt;
                height:8pt;
                border-width:1pt;
                padding-top:4px;
                width:30%;
                margin-left:35%;
            }
        `;
        const LeftImg = styled.img`
            position: relative;
            display:inline-flex;
            height:20px !important;
            width:10px !important;
            z-index:100;
            top:${-containerHeight/ rows /factor + 40}px;
            left:16%;
            border: 15px solid transparent;
            @media screen and (min-width: 0px) and (max-width: 885px) {
                left:2%;
                height:15px !important;
                width:8px !important;
                border: 5px solid transparent !important;
            }
            @media screen and (min-width: 885px) and (max-width: 1024px) {
                left:15%;
            }
        `;
        const RightImg = styled.img`
            position: relative;
            display:inline-flex;
            height:20px !important;
            width:10px !important;
            top:${-containerHeight/ rows /factor + 40}px;
            z-index:100;
            left:76%;
            border: 15px solid transparent;
            @media screen and (min-width: 0px) and (max-width: 885px) {
                left:89%;
                border: 5px solid transparent !important;
                height:15px !important;
                width:8px !important;
            }
            @media screen and (min-width: 885px) and (max-width: 1024px) {
                left:74%;
            }
        `;
        const FullScreenDiv = styled.div`
            background: rgba(0,0,0,0.5);
            position: fixed;
            z-index: 10000;
            top:0px;
            left:0px;
            width: 100%;
            height: 100%;
        `;

        let windowWidth = (window.innerWidth > 0) ? (window.innerWidth) : (window.outerWidth);
        let windowHeight = (window.innerHeight > 0) ? (window.innerHeight) : (window.outerHeight);
        let scorllFactor = 1024/683.0;
        const marginTop = (windowHeight - windowWidth/scorllFactor) / 2.0;

        const FullScreenCenterWrapper = styled.div`
            display:block;
            position:relative;
            margin-top: ${marginTop}px;
        `;

        var additional = "";
        if (this.state.zoomOutted) {
            additional =
            <FullScreenDiv onClick={this.zoomInFunc}> 
                <FullScreenCenterWrapper>
                    <TopScollImages autoPlay={false} imgs={this.state.gridData[this.state.currScollIndex].img} srcPrefix={SETTINGS.jctj.srcPrefix} clickToZoomOut={false} extraBottom={0}></TopScollImages>
                </FullScreenCenterWrapper>
            </FullScreenDiv>;
        }

        const WrapperDiv = styled.div`
            padding-bottom: ${this.state.extraBottom}px !important;
        `;

        return (
            <WrapperDiv>
                <OutterDiv>
                    <TitleBarDiv>{this.state.areaTitle}</TitleBarDiv>
                    <MiddleDiv>
                        <GridList cols={cols} row={rows} cellHeight="auto" spacing={0}>
                            {
                                this.state.gridData.map((data,idx) =>(
                                    <GridListTile key={idx}>
                                        <ImgListItem clickExtraFunc={() => this.imgClicked(idx)} srcPrefix={this.state.srcPrefix} img={data.img[0]} title={data.title} link={data.link}>
                                        </ImgListItem>
                                    </GridListTile>
                                    
                                ))
                            }
                        </GridList>
                    </MiddleDiv>
                </OutterDiv>
                <LeftImg src={require("../assets/media/icon_left.png")} alt="left" onClick={this.leftClick}></LeftImg>
                <RightImg src={require("../assets/media/icon_right.png")} alt="right" onClick={this.rightClick}></RightImg>
                {additional}
            </WrapperDiv>
        );
    }
}

export default TitleGridArea;