import React, {Component } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';
const OutterDiv = styled.div`
margin-left:20%;
margin-right:20%;
margin-top: 100pt;
@media screen and (min-width: 885px) and (max-width: 1024px) {
    margin-top:80pt;
}
@media screen and (min-width: 0px) and (max-width: 885px) {
    margin-top:40pt;
    margin-left:6%;
    margin-right:6%;
}
`;
class IframeComponent extends Component {

    constructor(props) {
        super(props);
        this.updateSize = this.updateSize.bind(this);
        this.state = {
          height: 200,
        }
      }
 
  componentDidMount() {
    this.updateSize();
    window.addEventListener('resize', this.updateSize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateSize);
  }
  
  updateSize() {
    try {
      const parentDom = ReactDOM.findDOMNode(this).parentNode;
      let w = parentDom.childNodes[0].getBoundingClientRect().width;
      let factor = 2.236140587;
      let height = w / factor;
      this.setState({ height });
    } catch (ignore) {
    }
  }


  render() {
    return (
      <OutterDiv>
        <iframe src={this.props.src}
          width="100%"
          height={this.state.height}
            className="fullheight"
            scrolling="no"
            frameBorder="0"
            allowFullScreen={true}
            title = {this.props.src}/>
      </OutterDiv>
    )
  }
}
export default IframeComponent;
