import React, { Component } from 'react';
import styled from 'styled-components'
import Carousel from 'rmc-m-carousel'
import { style } from '@material-ui/system';

class TopScrollImages extends Component {
    constructor(props) {
        super(props);
        this.onImageClicked = this.onImageClicked.bind(this);
        this.updateSize = this.updateSize.bind(this);
        this.state = {
            imgs: this.props.imgs,
            urls: this.props.urls,
            srcPrefix: this.props.srcPrefix,
            clickToZoomOut: this.props.clickToZoomOut,
            zoomOutted: false,
            extraBottom: this.props.extraBottom,
            autoplay: this.props.autoplay ? this.props.autoplay : true
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateSize);
      }
    
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateSize);
    }

    updateSize() {
        this.render();        
    }


    onImageClicked() {
        if (!this.state.clickToZoomOut) {
            return;
        } 
        this.setState({zoomOutted: !this.state.zoomOutted});
        this.render();
    }
    
    render() {
        const fromScale = this.state.clickToZoomOut ? (this.state.zoomOutted ? 1 : 1.02) : 1;
        const toSacle = this.state.clickToZoomOut ?  (this.state.zoomOutted ? 1.02 : 1) : 1;
        const marginLeft = this.state.clickToZoomOut ? (this.state.zoomOutted ? 8 : 20) : 20;
        const marginRight = this.state.clickToZoomOut ? (this.state.zoomOutted ? 8 : 20) : 20;
        let windowWidth = (window.innerWidth > 0) ? (window.innerWidth) : (window.outerWidth);
        let windowHeight = (window.innerHeight > 0) ? (window.innerHeight) : (window.outerHeight);
        let factor = 1024/683.0;
        const marginTop = this.state.clickToZoomOut ? (this.state.zoomOutted ? ("margin-top:" + (windowHeight - windowWidth/factor) / 2.0 + "px !important;") : "margin-top: 100pt;") : "margin-top: 100pt;";
        const animationDuration = this.state.clickToZoomOut ? 300 : 0;
        const CarouselWrapper = styled.div`
            @keyframes zoomAnimation {
                from {
                    transform: scale(${fromScale});
                }
                to {
                    transform: scale(${toSacle});
                }
            }
            ${marginTop}
            position:relative;
            margin-left:${marginLeft}%;
            margin-right:${marginRight}%;
            padding-bottom: ${this.state.extraBottom}px;

            animation-name: zoomAnimation;
            animation-duration: ${animationDuration}ms;
            animation-iteration-count: 1;
            animation-timing-function: ease-in-out;
            /* animation-fill-mode: forwards; */

            @media screen and (min-width: 885px) and (max-width: 1024px) {
                margin-top:80pt;
            }
            @media screen and (min-width: 0px) and (max-width: 885px) {
                margin-top:30pt;
                margin-left:6%;
                margin-right:6%;
            }
            .rmc_carousel_dot {
                position: relative !important;
                margin-top:3pt;
                bottom: 0% !important;

            }
            .rmc_carousel_dot_point.rmc_dot_point_active span{
                background: white !important;
            }
            .rmc_carousel_dot_point span {
                background: gray;
            }
        `;

        const FullScreenDiv = styled.div`
            background: rgba(0,0,0,0.5);
            position: fixed;
            z-index: 10000;
            top:0px;
            left:0px;
            width: 100%;
            height: 100%;
        `;
        
        var array = this.state.imgs;
        var generateHTML = [];
        for (let index = 0; index < array.length; index++) {
            const element = array[index] + '';
            if (this.state.urls !== undefined && this.state.urls[index] !== "") {
                generateHTML.push(
                    <a target="_blank" rel="noopener noreferrer" href={this.state.urls[index]}  key={index}>
                        <img src={require('../assets/media/topScroll/' + element + '')} alt="" onClick={this.onImageClicked}/>
                    </a>
                );
            } else {
                generateHTML.push(
                    <img key={index} src={require('../assets/media/' + this.state.srcPrefix + '/' + element)} alt="" onClick={this.onImageClicked}/>
                );
            }
        }

        if (this.state.zoomOutted) {
            return (
                <FullScreenDiv>
                    <CarouselWrapper>
                        <Carousel autoplay={false} interVal={999999999}>
                            {generateHTML}
                        </Carousel>
                    </CarouselWrapper>    
                </FullScreenDiv>
            );
        } else {
            return (
                <CarouselWrapper>
                    <Carousel autoplay={false} interVal={9999999999}>
                        {generateHTML}
                    </Carousel>
                </CarouselWrapper>          
            );
        }

    }
}
export default TopScrollImages;