import React, {Component } from 'react';
import styled from 'styled-components'
const OutterDiv = styled.div`
display:block;
position:relative;
`;
const InnerImg = styled.img`
display:block;
position:relative;
width:94%;
margin-left:3%;
margin-right:3%
`;
const InnerA = styled.a`
margin-top: 5pt;
display:block;
position:relative;
font-size:9pt;
color:white;
text-decoration:none;
text-align:center;
@media screen and (min-width: 0px) and (max-width: 885px) {
    font-size:5pt;
}
`;
class ImgListItem extends Component {
    constructor(props) {
        super(props);
        this.imageOnClick = this.imageOnClick.bind(this);
        this.state = {
            srcPrefix: this.props.srcPrefix,
            img: this.props.img,
            title: this.props.title,
            link: this.props.link,
            clickExtraFunc: this.props.clickExtraFunc
        };
    }

    imageOnClick() {
        if (this.state.clickExtraFunc) {
            this.state.clickExtraFunc();
        }
        if (this.state.link !== undefined && this.state.link !== "") {
            window.open(this.state.link);
        }
    }

    render() {
        var InnerAContent = "";    
        if (this.state.link !== undefined && this.state.link !== "") {
            InnerAContent = <InnerA href={this.state.link}>{this.state.title}</InnerA>
        } else {
            InnerAContent = <InnerA>{this.state.title}</InnerA>
        }
        return(
            <OutterDiv>
                <InnerImg src={require('../assets/media/' + this.state.srcPrefix + '/' + this.state.img)} alt="" onClick={this.imageOnClick}></InnerImg>
                {InnerAContent}
            </OutterDiv>
        );
    }
}

export default ImgListItem;